.settings{
    /* border: 1px solid black;*/
    margin-top: 20px;
    max-width: 1400px;
    margin: auto;
    display: flex;

} 
.inner-settings{
    border: 1px solid black;
    width: 400px;
    display: inline-block;
    background: #fafafa;
    padding: 20px;
}
textarea{
    min-height: 200px;
    min-width: 300px;
    overflow-y:scroll;
}
.how-to-use{
    border: 1px solid black;
    height: fit-content;
    border-radius: 5px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 20px;
}
@media(max-width:600px){
    .big-screen{
        display: none;
    }
    .small-screen{
        display: initial;
    }
    .inner-settings{
        padding-left: 10px;
        max-width: 300px;
    }
    .settings{
        border: 1px solid black;
        width: 100%;
        display: flex;
        justify-content: center;
    }
    .how-to-use{
        display: none;
    }
}