.navbar-container{
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 1400px;
    margin: auto;
}
small-screen{
    display: none;
}
.navbar{
    background-color: rgb(24,46,94);
    color: white;
}
.burger{
    display: none;
}
@media(max-width:600px){
    .big-screen{
        display: none;
    }
    .small-screen{
        display: initial;
    }
    .burger{
        display: block;
    }
    .how{
        position: absolute;
        border: 1px solid black;
        background-color: black;
        margin-top: 200px;

    }
}
